    <div class="rightCol">
      <div class="section-headline">{{ lang.experience }}</div>
      <div>
        <h5 class="headline">前端工程师 - 万达网络科技集团</h5>
        <p class="subheadline">2016年2月 - 至今</p>
      </div>
      <div class="block">
        <div class="block-helper"></div>
        <h3 class="headline">支付营销后台</h3>
        <p>
          使用Angular，从支付营销后台的一系列项目中，加深了对angular怎样组件化的理解。<br>
          团队很多人参与不同的Angular后台项目，每个项目都有头部导航，树形菜单，查询列表等，怎样在团队内共享这些通用的代码？四个思路：
        <ul>
          <li>1.提炼功能性组件，例如树形菜单就被提炼为一个Menu组件，只要传入菜单数据，简单配置即可生成一个可交互的树形菜单。</li>
          <li>
            2.提炼容器性组件，例如头部导航组件Nav，主要是传入数据然后展开为一段有样式的HTML，用于节省HTML和CSS的书写
          </li>
          <li>
            3. 提炼服务性组件，例如全局消息通知Message, 更像一个工具，按需注入使用
          </li>
          <li>
            4.抽象页面化组件，这是缩短开发时间的大杀器，例如整个列表页抽象为组件omnitable，传入一个配置对象，即可生成一个完整可交互的列表页（颇像现在vue的思路）。
          </li>
        </ul>
          现在这些组件被开源为
        <a href="http://www.fancyui.org/#/zh-cn/component/introduction">fancyui</a>
        </p>
      </div>
      <div class="block">
        <div class="block-helper"></div>
        <h3 class="headline">Beacon设计部署平台</h3>
        <p>
          负责项目的前端部分，使用Angular。从这个项目中，对Angular后台如何组织和复用代码有了更深刻的理解。<br>
          由于Beacon的设计是基于室内地图的，几乎每个页面都包含这个过程: 地图的实例化->拉取地图相关数据->配置->渲染地图->加载Beacon->绑定事件->添加Beacon交互，Controller基本都被这个过程的代码占据，十分冗长。而且不同页面重复代码过多，不易维护。<br>
          解决办法：把通用的一系列执行流程抽象为情景，通过配置和暴露钩子函数的方式来自定义情景，具体实现上每个情景为一个基类Controller；把Controller通用的功能抽象为装饰器，具体实现上每个功能为一个service，通过注入Controller来为Controller附加功能</p>
      </div>
      <div class="block">
        <div class="block-helper"></div>
        <h3 class="headline">广场室内地图移动端导览</h3>
        <p>
          整个单页面应用使用jQuery。从这个项目中加深了对单页面应用的理解。<br>
          项目起初功能比较简单，所以采用jQuery来快速实现，但是随着功能的添加，页面交互越来越复杂。开始思考怎样组织代码易于维护。<br>
          解决办法：把一组div的显示抽象为一个状态，一组div的隐藏到另一组div的显示抽象为一个状态的切换，基于这个想法，实现了一个状态管理器，定义每个状态的生命周期，从状态进入到状态销毁，把代码按状态分割，按生命周期顺序组织。</p>
      </div>
      <div class="block">
        <div class="block-helper"></div>
        <h3 class="headline">室内定位SDK和3D室内地图SDK</h3>
        <p>
          室内定位SDK是基于微信扫描到的Beacon来返回位置和方向信息的，由于不同手机硬件和OS的差别，踩坑无数，其中比较困难的几个问题：<br>
          很多广场是中空的，在一楼可能扫描到二楼的Beacon，这样就会导致定位点跳楼层的问题；微信是一秒返回一次扫描到的Beacon，而对方向的更新要求是实时的；H5设备旋转的坐标系和地图不一致，而且h5里是相对度数，0度的位置会变化，怎样变换成实际角度呢；异常情况的判别，微信能给出的异常只有蓝牙是否打开、位置权限是否打开、网络是否可用等，但是如果用户中途关闭蓝牙、或者某些低端Android机就是蓝牙已开也扫不到Beacon... 怎样判断这些情况并给出有意义的提示信息。<br>
          解决办法：记录历史楼层数据，当楼层变化且连续三次同一个数值的时候再执行跳楼层操作；初始值使用Beacon得到的角度，用设备旋转的变化值来计算实时角度，然后每秒用Beacon获取的方向值对计算出的方向进行矫正；设定一个Beacon接收中断的最大时间，超出这个时间判定为Beacon信息丢失，具体是蓝牙中途关闭还是已经离开Beacon部署区，需要根据手机地理位置进一步判断...</p>
      </div>
      <div class="block">
        <div class="block-helper"></div>
        <h3 class="headline">广场地图大屏导览和停车场寻车H5</h3>
        <p><!--TODO: 重新组织问题和解决办法-->
          使用Vue。之所以使用Vue，因为2D地图H5使用jQuery，发现对于复杂的单页应用，管理状态控制div的显示隐藏还是挺累人的，需要一个东西协助做这些事：把数据填充到页面（模版引擎的功能）；方便的绑定事件处理函数；当切换到某个状态时，自动显示隐藏某些div(vue-router 通过路由可以做这些事) 等。Vue满足这些需求，而且相比Angular更喜欢Vue的写法。<br>
          由于大屏可能位于地下的弱网环境，寻车H5在微信内，通过这两个项目，我感觉最重要的不是熟悉了Vue的使用，而是：<br>
        <ul>
          <li>
            1. 对 Progressive Web App 和性能优化的关注：怎样缩短首屏加载时间、怎样让长列表渲染更快、怎样做缓存等。针对请求，写了一个请求的库来解决缓存和请求的次序依赖问题<a
            href="https://github.com/xiaoshude/async-queue">async-queue</a>，对于有依赖的请求，在被依赖请求未完成前会暂存在队列里待合适时机发出，每个请求都会根据请求参数进行缓存。
          </li>
          <li>
            2. 加深了前端编写测试和代码可测试性的理解。如果无法在本地通过运行整个项目对代码执行结果得到即时反馈，这时编写单元测试是必要的，而且编写单测会促使尽可能使用纯函数，让代码组织更清晰可预测。
          </li>
          <li>
            3. 对微信内H5如何调试的探索。
          </li>
        </ul>
        </p>
      </div>
      <div style="margin-top: 15px">
