<template>
<div id="app">
  <div class="page-wrapper">
    <div class="page" id="material-dark">
      <div class="page-inner">
        <material-dark></material-dark>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'app'
};
</script>

<style>
/* Add new fonts here */
@import '../node_modules/font-awesome/css/font-awesome.css';
@import '../node_modules/material-design-icons/iconfont/material-icons.css';
body {
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  background: #CCCCCC;
}
#material-dark {
  height: auto;
}
.page-inner{
  height: 100%;
  width: 100%;
}
.page-wrapper {
  overflow-x: hidden;
  background: #CCCCCC;
  margin: 0;
  padding: 0;
  -webkit-print-color-adjust: exact;
  box-sizing: border-box;
}

.resume {
  height: 100%;
  width: 100%;
}

.page {
  background: white;
  position: relative;
  width: 21cm;
  height: 29.68cm;
  display: block;
  page-break-after: auto;
  overflow: hidden;
}
</style>



